
<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid text-start">
        <p class="fs-5 m-2">
          {{ this.$route.meta.title }}
        </p>
        <div v-if="!videoSession">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else>
          <div class="row g-0">
            <img
              class="col-12 col-md-2 rounded-0 rounded-start"
              :src="API_STORAGE_URL + product.main_image_path"
            />
            <div
              class="
                bg-primary
                col-12 col-md-10
                fs-2
                p-2
                rounded-0 rounded-end
                text-light
              "
            >
              {{ product.name }}
              <div v-if="!videoSession.free_conference_before_start_text">
                <div class="fs-4 mt-3">
                  جلسه
                  {{ videoSession.name }}
                </div>
                <div class="fs-6 mt-3">
                  روز های برگزاری:
                  {{ product.days }} , ساعت برگزاری: {{ product.hour }} , تاریخ
                  شروع:
                  {{ product.start_date }}
                </div>
              </div>
              <div v-else class="fs-4 mt-3">
                {{ videoSession.free_conference_before_start_text }}
              </div>
            </div>
          </div>

          <div class="mt-3">
            <div class="fs-3 text-center">
              <SessionStatusMessage
                :startDate="videoSession.video_session.start_date"
                :startTime="videoSession.video_session.start_time"
                :videoSessionType="
                  videoSession.video_session.video_session_type
                "
                :videoLink="videoSession.video_session.video_link"
                :productDetailViedosId="videoSession.id"
                :videoSessionName="videoSession.name"
                currentTime=""
              />
            </div>
          </div>

          <div class="row mt-3">
            <div class="col">
              <div class="card">
                <div class="card-header fs-4">دریافت جزوه</div>
                <div class="card-body d-flex justify-content-start">
                  <div
                    v-for="item in videoSession.files"
                    :key="item.id"
                    class="
                      border
                      d-flex
                      justify-content-between
                      m-1
                      p-2
                      rounded-1
                      fs-6
                    "
                  >
                    <a :href="API_STORAGE_URL + item.file_path" target="_blank">
                      <i class="fas fa-download"></i>
                      {{ item.name }}
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card">
                <div class="card-header fs-4">ارسال تکلیف</div>
                <div class="card-body">
                  <div v-if="homeworkError" class="alert alert-danger">
                    {{ homeworkError }}
                  </div>
                  <input
                    class="form-control"
                    type="file"
                    @change="addUploadFile($event)"
                  />
                  <input
                    class="form-control my-2"
                    type="text"
                    placeholder="* توضیحات تکلیف"
                    v-model="homeworkDescription"
                  />
                  <a
                    class="btn btn-sm border rounded mt-1"
                    @click="setHomework"
                  >
                    ارسال
                  </a>
                  <span v-show="homeworkSpin">
                    <i class="fas fa-spinner fa-spin"></i>
                    <span>{{ this.$store.state.uploadPercentage }}</span>
                  </span>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card">
                <div class="card-header fs-4">آزمون کلاسی</div>
                <div class="card-body">
                  <a class="btn w-100 shadow border rounded my-3">
                    انجام آزمون کلاسی
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div class="row g-0 mt-3">
            <div class="col-12 col-md-6">
              <div class="card">
                <div class="card-header fs-5 text-center">
                  کلیه جلسات این محصول
                </div>
                <div class="card-body">
                  <VideoSessions :productId="Number(productId)" />
                </div>
              </div>
            </div>

            <div class="col-12 col-md-6">
              <div class="card m-0 ms-4">
                <div class="card-header fs-5 text-center">ارسال نظر</div>
                <div class="card-body">
                  <textarea
                    v-model="productComment"
                    class="form-control"
                    rows="10"
                  ></textarea>
                  <button class="btn btn-primary mt-2" @click="addComment">
                    ثبت نظر
                  </button>
                  <span v-show="commentSpin">
                    <i class="fas fa-spinner fa-spin"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "@/libs/api.js";
import utils from "@/libs/utils";
import VideoSessions from "@/components/VideoSessions";
import SessionStatusMessage from "./SessionStatusMessage";

export default {
  name: "VideoSession",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
    this.videoSessionId = this.$route.params.videoSessionId;
    this.productId = this.$route.params.productId;
    this.getUserProduct();
    this.getUserProductVideoSessionInfo();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
    VideoSessions,
    SessionStatusMessage,
  },
  data() {
    return {
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
      product: null,
      videoSessionList: null,
      videoSession: null,
      productComment: null,
      commentSpin: false,
      homeworkFile: null,
      homeworkSpin: false,
      homeworkDescription: null,
      homeworkError: null,
    };
  },
  methods: {
    getUserProduct() {
      this.arefApi
        .getUserProduct(this.productId)
        .then((resp) => {
          this.product = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getUserProductVideoSessionInfo() {
      this.arefApi
        .getUserProductVideoSessionInfo(this.videoSessionId)
        .then((resp) => {
          this.videoSession = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    addComment(event) {
      if (!this.productComment) {
        this.$swal.fire({
          title: "!خطا",
          text: "متن نظر را وارد کنید",
          icon: "error",
          confirmButtonText: "متوجه شدم",
        });
        return;
      }
      this.commentSpin = true;
      event.target.disabled = true;
      const commentData = {
        products_id: this.product.id,
        comment: this.productComment,
      };
      this.arefApi
        .addProductComment(commentData)
        .then(() => {
          this.productComment = null;
          this.utils.showSuccess(
            this,
            "ثبت با موفقیت انجام شد ، پس از تایید مدیریت منتشر خواهد شد"
          );
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          event.target.disabled = false;
          this.commentSpin = false;
        });
    },
    addUploadFile(event) {
      this.homeworkFile = event.target.files[0];
    },
    setHomework() {
      if (!this.validateHomeworkData()) {
        return;
      }
      this.homeworkSpin = true;
      const dataObj = {
        description: this.homeworkDescription,
      };
      this.arefApi
        .setHomeworkDescription(dataObj, this.videoSession.video_session.id)
        .then((resp) => {
          this.setHomeworkFile(resp.data.id);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
          this.homeworkSpin = false;
        });
    },
    validateHomeworkData() {
      const allowedMimeTypes = [
        "application/msword", //doc
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document", //docx
        "application/pdf", //pdf
        "application/vnd.ms-excel", //xls
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet", //xlsx
      ];

      if (!this.homeworkFile) {
        this.homeworkError = "فایل تکلیف را الصاق نمایید";
        return false;
      }

      if (allowedMimeTypes.indexOf(this.homeworkFile.type) < 0) {
        this.homeworkError =
          "فایل الصاقی مجاز نمی باشد.انواع مجاز xlx , xlsx , pdf , doc , docx می باشد";
        return false;
      }

      if (this.homeworkFile.size > 1048576) {
        this.homeworkError = "حجم فایل الصاقی بیشتر از 10 مگابایت است";
        return false;
      }

      if (!this.homeworkDescription) {
        this.homeworkError = "توضیحات تکلیف را وارد نمایید";
        return false;
      }
      return true;
    },
    setHomeworkFile(user_video_session_homeworks_id) {
      let formDataTmp = new FormData();
      formDataTmp.append("file", this.homeworkFile);
      this.arefApi
        .attackHomeworkToVideoSession(
          formDataTmp,
          user_video_session_homeworks_id
        )
        .then(() => {
          this.utils.showSuccess(this);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          this.homeworkSpin = false;
        });
    },
  },
  mounted() {},
  watch: {
    $route(to, from) {
      if (
        to !== from &&
        to.name === "VideoSession" &&
        from.name === "VideoSession"
      ) {
        this.$router.push({ path: to.path });
        this.$router.go();
      }
    },
  },
};
</script>

